import React, { useState } from 'react';

export default function TextForm(props) {
  const [text, setText] = useState('');
  const [history, setHistory] = useState([text]);
  const [redoHistory, setRedoHistory] = useState([]);
  const [historyIndex, setHistoryIndex] = useState(0);

  const updateHistory = (newText) => {
    const newHistory = history.slice(0, historyIndex + 1);
    newHistory.push(newText);
    setHistory(newHistory);
    setHistoryIndex(newHistory.length - 1);
    setRedoHistory([]);
  };

  const handleUpClick = () => {
    const newText = text.toUpperCase();
    updateHistory(newText);
    setText(newText);
    props.showAlert("Converted to uppercase!", "success");
  };

  const handleLoClick = () => {
    const newText = text.toLowerCase();
    updateHistory(newText);
    setText(newText);
    props.showAlert("Converted to lowercase!", "success");
  };

  const handleClearClick = () => {
    const newText = '';
    updateHistory(newText);
    setText(newText);
    props.showAlert("Text has been cleared successfully!", "success");
  };

  const handleOnchange = (event) => {
    const newText = event.target.value;
    updateHistory(newText);
    setText(newText);
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(text);
    props.showAlert("Text copied to Clipboard!", "success");
  };

  const handleExtraSpaces = () => {
    const newText = text.split(/[ ]+/).join(' ');
    updateHistory(newText);
    setText(newText);
    props.showAlert("Extra Space has been removed!", "success");
  };

  const handlePasteFromClipboard = async () => {
    try {
      const clipboardText = await navigator.clipboard.readText();
      updateHistory(clipboardText);
      setText(clipboardText);
      props.showAlert('Text pasted from clipboard!', 'success');
    } catch (error) {
      props.showAlert('Failed to read clipboard contents', 'danger');
    }
  };

  const handleExtractNumbers = () => {
    const numbers = text.match(/\d+/g);
    if (numbers) {
      const newText = numbers.join(' ');
      updateHistory(newText);
      setText(newText);
      props.showAlert("Numbers have been extracted!", "success");
    } else {
      props.showAlert("No numbers found!", "warning");
    }
  };

  const handleListenText = () => {
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(text);
      window.speechSynthesis.speak(utterance);
      props.showAlert("Listening to text!", "success");
    } else {
      props.showAlert("Speech Synthesis not supported in this browser.", "danger");
    }
  };

  const handleUndo = () => {
    if (historyIndex > 0) {
      const newHistoryIndex = historyIndex - 1;
      const newText = history[newHistoryIndex];
      setText(newText);
      setRedoHistory([text, ...redoHistory]);
      setHistoryIndex(newHistoryIndex);
      props.showAlert("Undone to previous state", "success");
    } else {
      props.showAlert("No more actions to undo!", "warning");
    }
  };

  const handleRedo = () => {
    if (redoHistory.length > 0) {
      const newText = redoHistory[0];
      setText(newText);
      setHistoryIndex(historyIndex + 1);
      setHistory([...history, newText]);
      setRedoHistory(redoHistory.slice(1));
      props.showAlert("Redone to next state", "success");
    } else {
      props.showAlert("No more actions to redo!", "warning");
    }
  };

  const wordCount = text.split(/\s+/).filter((element) => element.length !== 0).length;
  const charCount = text.length;
  const readTime = 0.008 * wordCount;

  return (
    <>
      <div className="container" style={{ color: props.mode === 'dark' ? 'white' : '#070F2B' }}>
        <h3 className='mb-2'>{props.heading}</h3>
        <div className="mb-2">
          <textarea
            className="form-control"
            value={text}
            onChange={handleOnchange}
            style={{ backgroundColor: props.mode === 'dark' ? '#13466e' : 'white', color: props.mode === 'dark' ? 'white' : '#070F2B' }}
            id="myBox"
            rows="8"
          />
        </div>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleUpClick}>Convert to UpperCase</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleLoClick}>Convert to LowerCase</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleClearClick}>Clear text</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleCopy}>Copy text</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
        <button className="btn btn-primary mx-1 my-1" onClick={handlePasteFromClipboard}>Paste from Clipboard</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleExtractNumbers}>Extract Numbers</button>
        <button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleListenText}>Listen to Text</button>
        <button className="btn btn-primary mx-1 my-1" onClick={handleUndo}>Undo Action</button>
        <button className="btn btn-primary mx-1 my-1" onClick={handleRedo}>Redo Action</button>
      </div>

      <div className="container my-3" style={{ color: props.mode === 'dark' ? 'white' : '#070F2B' }}>
        <h3>Your text summary</h3>
        <p><b>{wordCount} words and {charCount} characters</b></p>
        <p><b>{readTime} Minutes read</b></p>
        <h3>Preview</h3>
        <p>{text.length > 0 ? text : "Nothing to preview!"}</p>
      </div>
    </>
  );
}
