import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

const Footer = () => {
  return (
    <footer style={footerStyle}>
      Made with <span role="img" aria-label="heart"> ❤️ </span> by Sayan-Roy
      <a href='https://github.com/RoyXSayan' style={linkStyle} target='_blank' rel="noopener noreferrer">
        <FontAwesomeIcon icon={faGithub} style={iconStyle} />
      </a>
    </footer>
  );
};

const footerStyle = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  position: 'fixed',
  bottom: '0',
  width: '100%',
  backgroundColor: '#f1f1f1',
  textAlign: 'center',
  padding: '4px',
  zIndex: '1000',
  fontSize: '15px',
};

const linkStyle = {
  marginLeft: '10px',
  color: 'black',
  textDecoration: 'none',
};

const iconStyle = {
  marginLeft: '5px',
  fontSize: '18px',
};

export default Footer;
